"use client";

import { useEffect, useState } from "react";
import { motion } from "framer-motion";

interface ScrambleTextProps {
    text: string;
    className?: string;
}

const CHARS = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789!@#$%^&*()_+";

export default function ScrambleText({ text, className }: ScrambleTextProps) {
    const [displayText, setDisplayText] = useState("");
    const [isScrambling, setIsScrambling] = useState(false);

    useEffect(() => {
        let interval: NodeJS.Timeout;
        let counter = 0;

        const startScramble = () => {
            setIsScrambling(true);
            interval = setInterval(() => {
                const scrambled = text
                    .split("")
                    .map((char, index) => {
                        if (index < counter) {
                            return text[index];
                        }
                        return CHARS[Math.floor(Math.random() * CHARS.length)];
                    })
                    .join("");

                setDisplayText(scrambled);
                counter += 1 / 3; // Speed control

                if (counter >= text.length) {
                    clearInterval(interval);
                    setIsScrambling(false);
                    setDisplayText(text);
                }
            }, 30);
        };

        // Use IntersectionObserver logic outside of this component typically,
        // but here we can just trigger it once mounted or passed a trigger.
        // Ideally this component should expose a trigger or handle viewport itself.
        // For simplicity, we run on mount/viewport.

        startScramble();

        return () => clearInterval(interval);
    }, [text]);

    return (
        <span className={className}>{displayText}</span>
    );
}
